.liststyle {
    color: whitesmoke;
    width: 300px;
    background-color: palevioletred;
    height: 50px;
    margin: 20px auto;
    border-radius: 5px;
}

.liststyle p {
    padding: 10px;
    position: relative;
}

.liststyle p span {
    position: absolute;
    right: 10px;
    cursor: pointer;
}

.liststyle p input {
    background-color: transparent;
    border: 0;
    color: #fff;
}